console.log('group.js');
import $ from 'jquery';
import dropdown from '../../js/dropdown.js'
import hexGrid from '../../js/hexGrid.js'
import mobileMenu from '../../js/mobileMenu.js'

$(document).ready(function(){
	(function(){
		$('.product-card__table-content').hide();
		var contentId = $('.product-card__table-tab--active').data('id');
		$('#'+contentId).show();
		$('.product-card__table-tab').on('click', function(){
			$('.product-card__table-tab').removeClass('product-card__table-tab--active');
			$('.product-card__table-content').hide();
			var contentId = $(this).data('id');
			$(this).addClass('product-card__table-tab--active');
			$('#'+contentId).show();
		})
	})()
	mobileMenu().init();
	dropdown().init();
	hexGrid().init();
})



